/* ------------------------------------------------------------------------------------
    General styles
 --------------------------------------------------------------------------------------- */
.home h3 i {
    display: block;
    color: $white;
    @media screen and ($bp-960-min) {
        display: none;
    }
}
.hpheading {
    color: $white;
    text-align: center;
    background-color: $dark-grey;
    border-top: 6px solid $light-grey;
    border-bottom: 6px solid $red;
    padding: 26px 0px 6px 0px;
    margin: 0 0;
    line-height: 22px;
    @media screen and ($bp-960-min) {
        color: $dark-grey;
        text-align: left;
        background: none;
        border: 0;
        padding: 0 25px 0 0;
        position: relative;
        display: inline-block;
        text-transform: uppercase;
        &:after {
            content: "";
            height: 4px;
            position: absolute;
            width: 1920px;
            top: 48%;
            left: 100%;
            background-color: $dark-grey;
        }
    }
}
/* ------------------------------------------------------------------------------------------------------------------------------
  Featured Content Area
--------------------------------------------------------------------------------------------------------------------------------- */
.featuredcontent {
    margin-top: -20px;
    max-width: $max-width-desktop;
    @media screen and ($bp-960-min) {
        margin: 25px auto 0 auto;
        padding-left: 2.5%;
        padding-right: 2.5%;
    }
    @media screen and ($bp-1200-min) {
        padding: 0;
    }
    h2 {
        margin: 10px 0 0 0;
    }
}
.ft_content {
    position: relative;
    @media screen and ($bp-960-min) {
        img {
            width: 65%;
        }
    }
    @media screen and ($bp-1200-min) {
        h2 {
            font-size: $fs-large;
        }
    }
}
.ft_desc {
    padding: 12px;
    box-sizing: border-box;
    border-top: 4px solid $dark-grey;
    @media screen and ($bp-960-min) {
        display: inline-block;
        width: 35%;
        height: 100%;
        position: absolute;
        bottom: 0;
        right: 0;
        border-bottom: 4px solid $dark-grey;
    }
    @media screen and (min-width: 1030px) {
        padding: 25px;
    }
}
/* ------------------------------------------
  Remaining Content
--------------------------------------------- */
.not_featured section {
    @media screen and ($bp-960-min) {
        margin: 50px auto;
        padding-left: 2.5%;
        padding-right: 2.5%;
        box-sizing: border-box;
    }
    @media screen and ($bp-1200-min) {
        padding-left: 0%;
        padding-right: 0%;
    }
}

// Reusable Design Variation 1
.design-1 {
    position: relative;
    max-width: $max-width-desktop;
    @media screen and ($bp-960-min) {
        margin-top: 25px;
    }
}
.design-1 .item {
    @media screen and ($bp-960-min) {
        $right-margin: 2%;
        width: (100 - ($right-margin * 2)) / 3;
        float: left;
        margin-right: #{$right-margin};
        img {
            opacity: .8;
        }
        &:last-of-type {
            margin-right: 0;
        }
        &:hover img {
            opacity: 1;
        }
    }
}
.design-1 .desc {
    padding: 15px 15px 20px 15px;
    position: relative;
    top: -70px;
    margin-bottom: -70px;
    background: $white;
    @media screen and ($bp-960-min) {
        padding: 15px 5px 15px 0;
        top: 0;
        margin-bottom: 0;
    }
    h2 {
        margin: 8px 0 0 0;
    }
    p {
        margin: 0;
        padding: 8px 0;
    }
}

/* ------------------------------------------
  New Releases
--------------------------------------------- */
.newreleases {
    background: $dark-grey;
    margin-top: 25px;
    @media screen and ($bp-960-min) {
        padding: 45px 0 60px 0;
        .container {
            max-width: $max-width-desktop;
            margin: 0 auto;
        }
    }
    h3 {
        color: $white;
        &:after {
            background: $white;
        }
    }
}
 div.album {
    position: relative;
    .albumimg {
        width: 50%;
    }
    @media screen and ($bp-960) {
        border-bottom: 4px solid $white;
        &:last-of-type {
            border: 0;
        }
        &.ct-1 .albumdesc, &.ct-3 .albumdesc {
            margin-left: 50%;
        }
        &.ct-2 .albumimg, &.ct-4 .albumimg {
            margin-left: 50%;
        }
    }
    @media screen and ($bp-960-min) {
        width: 24%;
        float: left;
        margin-right: 1.25%;
        .albumimg {
            width: 100%;
            overflow: hidden;
        }
        &:nth-last-of-type(1) {
            margin-right: 0;
        }
        &:hover img {
            transform: scale(1.1);
            transition: 1s;
        }
    }
}
.albumdesc {
    position: absolute;
    top: 0;
    height: 100%;
    padding: 25px;
    box-sizing: border-box;
    background: $light-grey-transparent;
    width: 50%;
    h2, p {
        color: $white;
    }
    a {
        display: block;
        height: 100%;
        position: relative;
    }
    p {
        @media screen and (max-width: 575px) {
            display: none;
        }
        @media screen and ($bp-960-min) and ($bp-1200) {
            display: none;
        }
    }
    h2 {
        margin-top: 0;
    }
    .read-more {
        background: none;
        font-weight: 700;
        padding-left: 0;
        position: absolute;
    }
    @media screen and ($bp-960-min) {
        opacity: 0;
        width: 100%;
        .read-more {
            bottom: 0;
        }
        .album:hover & {
            opacity: 1;
            transition: .5s;
        }
    }
}